export const __metadata = { PACKAGE_NAME: '@wix/stores' };

/** @internal */
export function queryVariants(httpClient: HttpClient): QueryVariantsSignature {
  return (options?: QueryVariantsOptions) =>
    universalQueryVariants(
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface QueryVariantsSignature {
  /**
   * Retrieves a list of ReadOnlyVariants, given the provided [paging, filtering, and sorting][1].
   * Up to 1,000 ReadOnlyVariants can be returned per request.
   * To learn how to query ReadOnlyVariants, see [API Query Language][2].
   * [1]: https://dev.wix.com/api/rest/getting-started/sorting-and-paging
   * [2]: https://dev.wix.com/api/rest/getting-started/api-query-language
   */
  (options?: QueryVariantsOptions | undefined): VariantsQueryBuilder;
}

/** @internal */
export function searchVariants(
  httpClient: HttpClient
): SearchVariantsSignature {
  return (search: ReadOnlyVariantSearch, options?: SearchVariantsOptions) =>
    universalSearchVariants(
      search,
      options,
      // @ts-ignore
      { httpClient }
    );
}

interface SearchVariantsSignature {
  /**
   * Search Variants using [WQL - Wix Query Language](https://dev.wix.com/api/rest/getting-started/api-query-language)
   * >**Notes:**
   * + If you don't have permissions to read non visible products then variants related to non-visible products will be filtered out from response.
   * In such case non visible variants also will be filtered out.
   * @param - WQL expression
   */
  (
    search: ReadOnlyVariantSearch,
    options?: SearchVariantsOptions | undefined
  ): Promise<SearchVariantsResponse>;
}

